import Input from './components/Input.vue' // 
import Pwdform from './components/Pwdform.vue'
import Progress from './components/Progress.vue'
import Calendar from './components/Calendar.vue'
const paykeyboard = {
    install(Vue, options) {
        Vue.component(Input.name, Input) // vuePayKeyboard.name name
        Vue.component(Progress.name, Progress)
        Vue.component(Pwdform.name, Pwdform)
        Vue.component(Calendar.name, Calendar)
        //  this.$xxx  
        // Vue.prototype.$xxx  //  this.$xxx 
        // $  
    }
}
if (typeof window !== 'undefined' && window.Vue) {
    window.Vue.use(paykeyboard);
}
export default paykeyboard // ..


